.App {
  text-align: center;
}

.link{
  background-color: purple;
  width: 200px;
  height: 60px;
  font-size: 23px;
  line-height: 60px;
  text-decoration: none;
  color: white;
  display: inline-block;
  border: 1px solid white;
}

.link2{
  color: white;
  text-decoration: none;
}

.link2:hover {
  color: goldenrod;
}

.link:hover {
  color: purple;
  background-color: white;
  border: 1px solid purple;
}

.content {
  display: inline-block;
  width: 806px;
  height: 100vh;
  color: white;
  font-size: 23px;
  text-align: center;
  background-color: purple;
}

.link3 {
  text-decoration: none;
  color: goldenrod;
  margin: 0;
  display: block;
}


.customTable {
  width: 100%;
  height: auto;
  background-color: lightgrey;
}

.customTable > thead > tr > th{
  padding: 15px;
  font-size: 23px;
}

.customTable > thead > tr > th:nth-child(2){
  padding: 15px;
  width: 200px;
  font-size: 23px;
}

.customTable > thead > tr > th:nth-child(3){
  padding: 15px;
  width: auto;
  font-size: 23px;
}

.customTable > thead > tr > th:nth-child(4){
  padding: 15px;
  width: 300px;
  font-size: 23px;
}

.customTable > tbody > tr > td{
  padding: 8px;
}

/* ALternate row different color */
.customTable > tbody > tr:nth-child(even){
  background-color: purple;
  color: white;
}
.customTable > tbody > tr:nth-child(odd){
  background-color: white;
  color: purple;
}
/* ALternate row different color */

.customTable > tbody > tr > td:nth-child(1){
  padding: 15px;
  width: 1px;
  font-size: 23px;
}

.customTable > tbody > tr > td:nth-child(2){
  padding: 15px;
  width: 200px;
  font-size: 23px;
}

.customTable > tbody > tr > td:nth-child(3){
  padding: 15px;
  width: auto;
  font-size: 23px;
}

.customTable > tbody > tr > td:nth-child(4){
  padding: 15px;
  width: 300px;
  font-size: 23px;
}

input[type=checkbox]:not(#mainCheckbox) {
  width: 25px;
  height: 25px;
}

#mainCheckbox {
  width:30px;
  height: 30px;
}

.getselecteddataBtn {
  width: 100%;
  height: 70px;
  font-size: 30px;
  color: white;
  background-color: purple;
}